// =============================================================================
// Rate Limiting (Node.js Runtime)
// =============================================================================
// Node.js version that can access the database for API key-specific limits
// Use this in API routes (not middleware)

import { NextRequest, NextResponse } from 'next/server';
import { findApiKeyByToken, type ApiKey } from '@/db/api-keys';
import { extractApiKey } from '@/lib/auth';
import { createRequestLogger } from '@/lib/request-id';

// In-memory rate limit store
// Format: { key: { count: number, resetAt: number } }
const rateLimitStore = new Map<string, { count: number; resetAt: number }>();

// Cleanup old entries every 5 minutes
setInterval(() => {
  const now = Date.now();
  const keysToDelete: string[] = [];
  rateLimitStore.forEach((value, key) => {
    if (value.resetAt < now) {
      keysToDelete.push(key);
    }
  });
  keysToDelete.forEach(key => rateLimitStore.delete(key));
}, 5 * 60 * 1000);

export interface RateLimitConfig {
  windowMs: number; // Time window in milliseconds
  maxRequests: number; // Maximum requests per window
  identifier: string; // Unique identifier for this limit
}

export interface RateLimitResult {
  allowed: boolean;
  remaining: number;
  resetAt: number;
  limit: number;
}

/**
 * Check rate limit for a given identifier
 */
function checkRateLimit(config: RateLimitConfig): RateLimitResult {
  const now = Date.now();
  const key = config.identifier;
  const stored = rateLimitStore.get(key);

  // If no stored data or window expired, create new entry
  if (!stored || stored.resetAt < now) {
    const resetAt = now + config.windowMs;
    rateLimitStore.set(key, { count: 1, resetAt });
    return {
      allowed: true,
      remaining: config.maxRequests - 1,
      resetAt,
      limit: config.maxRequests,
    };
  }

  // Check if limit exceeded
  if (stored.count >= config.maxRequests) {
    return {
      allowed: false,
      remaining: 0,
      resetAt: stored.resetAt,
      limit: config.maxRequests,
    };
  }

  // Increment count
  stored.count++;
  rateLimitStore.set(key, stored);

  return {
    allowed: true,
    remaining: config.maxRequests - stored.count,
    resetAt: stored.resetAt,
    limit: config.maxRequests,
  };
}

/**
 * Rate limit by API key (Node.js version - can access database)
 * Looks up the API key's rateLimit from the database
 */
export async function rateLimitByApiKey(
  request: NextRequest,
  defaultLimit: number = 1000
): Promise<RateLimitResult | null> {
  const token = extractApiKey(request);
  if (!token) {
    return null; // No API key, skip API key rate limiting
  }

  // Find API key in database to get its rate limit
  let apiKey: ApiKey | null = null;
  try {
    apiKey = await findApiKeyByToken(token);
  } catch (error) {
    console.error('[rate-limit] Failed to lookup API key:', error);
    // Fall back to default limit if lookup fails
  }

  const limit = apiKey?.rateLimit || defaultLimit;
  
  // Use token hash as identifier (don't store full token)
  const tokenHash = token.substring(0, 16); // Use first 16 chars as identifier
  const config: RateLimitConfig = {
    windowMs: 60 * 60 * 1000, // 1 hour
    maxRequests: limit,
    identifier: `api_key:${tokenHash}`,
  };

  return checkRateLimit(config);
}

/**
 * Add rate limit headers to response
 */
export function addRateLimitHeaders(
  response: NextResponse,
  result: RateLimitResult
): NextResponse {
  response.headers.set('X-RateLimit-Limit', result.limit.toString());
  response.headers.set('X-RateLimit-Remaining', result.remaining.toString());
  response.headers.set(
    'X-RateLimit-Reset',
    new Date(result.resetAt).toISOString()
  );
  return response;
}

/**
 * Create rate limit error response
 */
export function createRateLimitResponse(
  result: RateLimitResult,
  request?: NextRequest,
  apiKeyId?: string,
  route?: string
): NextResponse {
  const resetDate = new Date(result.resetAt).toISOString();
  
  // Structured logging for rate limit events
  if (request) {
    const logger = createRequestLogger('rate-limit', {
      method: request.method,
      path: route || request.nextUrl.pathname,
      apiKeyId,
    });
    
    logger.warn({
      rateLimitExceeded: true,
      limit: result.limit,
      remaining: result.remaining,
      resetAt: resetDate,
      apiKeyId,
      route: route || request.nextUrl.pathname,
      ip: request.ip || request.headers.get('x-forwarded-for')?.split(',')[0]?.trim() || 'unknown',
    }, 'Rate limit exceeded');
  }
  
  const response = NextResponse.json(
    {
      error: 'Rate limit exceeded',
      code: 'RATE_LIMIT_EXCEEDED',
      message: `Too many requests. Limit: ${result.limit} per hour. Try again after ${resetDate}`,
      retryAfter: Math.ceil((result.resetAt - Date.now()) / 1000),
    },
    { status: 429 }
  );

  return addRateLimitHeaders(response, result);
}

