import { NextRequest, NextResponse } from 'next/server';
import {
  createEndpoint,
  listEndpoints,
  type MeteredEndpoint,
} from '@/db/metered-endpoints';
import { requireAuth } from '@/lib/auth';
import { createEndpointSchema } from '@/lib/schemas';
import { validateRequestBody, createValidationErrorResponse } from '@/lib/validate-request';
import { validateEndpointId } from '@/lib/input-validator';
import { rateLimitByApiKey, addRateLimitHeaders, createRateLimitResponse } from '@/lib/rate-limit-node';
import { extractRequestContext, wrapResponse, createErrorResponse } from '@/lib/request-logging';

/**
 * POST /api/v1/endpoints
 * 
 * Create a new metered endpoint
 * Requires: Authentication (user role minimum)
 */
export async function POST(request: NextRequest) {
  const ctx = await extractRequestContext(request, '/api/v1/endpoints');
  
  try {
    // Check rate limit (per API key)
    const rateLimitResult = await rateLimitByApiKey(request);
    if (rateLimitResult && !rateLimitResult.allowed) {
      const response = createRateLimitResponse(rateLimitResult, request, ctx.apiKeyId, '/api/v1/endpoints');
      return wrapResponse(response, ctx);
    }

    // Require authentication
    const authResult = await requireAuth(request, 'user');
    if ('response' in authResult) {
      return wrapResponse(authResult.response, ctx);
    }

    const { apiKey } = authResult;
    // Update context with API key ID
    ctx.apiKeyId = apiKey.id;
    
    // Validate request body with Zod schema
    const validation = await validateRequestBody(request, createEndpointSchema);
    if (!validation.valid) {
      return createValidationErrorResponse(validation);
    }

    const {
      name,
      description,
      upstreamUrl,
      price,
      network = 'base',
      asset,
    } = validation.data;

    // Additional validation for recipient address (required but not in schema)
    // This will be handled by the createEndpoint function
    const body = await request.json();
    const recipientAddress = body.recipientAddress;

    if (!recipientAddress) {
      return NextResponse.json(
        {
          error: 'Missing required field',
          code: 'VALIDATION_ERROR',
          details: [{ field: 'recipientAddress', message: 'recipientAddress is required', code: 'required' }],
        },
        { status: 400 }
      );
    }

    // Validate recipient address format (Ethereum address)
    const ethAddressRegex = /^0x[a-fA-F0-9]{40}$/;
    if (!ethAddressRegex.test(recipientAddress)) {
      return NextResponse.json(
        {
          error: 'Invalid recipient address',
          code: 'VALIDATION_ERROR',
          details: [{ field: 'recipientAddress', message: 'Must be a valid Ethereum address (0x followed by 40 hex characters)', code: 'invalid_format' }],
        },
        { status: 400 }
      );
    }

    // Validate that recipient is not the USDC contract (blacklisted)
    const USDC_CONTRACT = '0x833589fCD6eDb6E08f4c7C32D4f71b54bdA02913';
    if (recipientAddress.toLowerCase() === USDC_CONTRACT.toLowerCase()) {
      return NextResponse.json(
        {
          error: 'Invalid recipient address',
          code: 'VALIDATION_ERROR',
          details: [{ field: 'recipientAddress', message: 'Cannot be the USDC contract address (blacklisted). Use your wallet address instead.', code: 'blacklisted' }],
        },
        { status: 400 }
      );
    }

    // Use asset from schema or default
    const tokenAddress = asset || '0x833589fCD6eDb6E08f4c7C32D4f71b54bdA02913';
    const tokenSymbol = 'USDC';
    const chainId = 8453;

    // Create endpoint (use API key ID as createdBy)
    const endpoint = await createEndpoint({
      name,
      description,
      upstreamUrl,
      price: price.toString(),
      network,
      tokenAddress,
      tokenSymbol,
      chainId,
      recipientAddress,
      status: 'active',
      createdBy: apiKey.id, // Use API key ID as creator
    });

    const response = NextResponse.json({
      success: true,
      endpoint,
    });

    // Add rate limit headers
    if (rateLimitResult) {
      addRateLimitHeaders(response, rateLimitResult);
    }

    return wrapResponse(response, ctx);
  } catch (error) {
    return createErrorResponse(
      ctx,
      error instanceof Error ? error : new Error('Failed to create endpoint'),
      500,
      'INTERNAL_ERROR'
    );
  }
}

/**
 * GET /api/v1/endpoints
 * 
 * List all endpoints (with optional filters)
 * Requires: Authentication (read-only role minimum)
 */
export async function GET(request: NextRequest) {
  const ctx = await extractRequestContext(request, '/api/v1/endpoints');
  
  try {
    // Check rate limit (per API key)
    const rateLimitResult = await rateLimitByApiKey(request);
    if (rateLimitResult && !rateLimitResult.allowed) {
      const response = createRateLimitResponse(rateLimitResult, request, ctx.apiKeyId, '/api/v1/endpoints');
      return wrapResponse(response, ctx);
    }

    // Require authentication
    const authResult = await requireAuth(request, 'read-only');
    if ('response' in authResult) {
      return wrapResponse(authResult.response, ctx);
    }
    
    // Update context with API key ID
    ctx.apiKeyId = authResult.apiKey.id;
    const { searchParams } = new URL(request.url);
    const status = searchParams.get('status');
    const createdBy = searchParams.get('createdBy');

    const filters: { status?: string; createdBy?: string } = {};
    if (status) filters.status = status;
    if (createdBy) filters.createdBy = createdBy;

    const endpoints = await listEndpoints(filters);

    const response = NextResponse.json({
      success: true,
      endpoints,
      count: endpoints.length,
    });

    // Add rate limit headers
    if (rateLimitResult) {
      addRateLimitHeaders(response, rateLimitResult);
    }

    return wrapResponse(response, ctx);
  } catch (error) {
    return createErrorResponse(
      ctx,
      error instanceof Error ? error : new Error('Failed to list endpoints'),
      500,
      'INTERNAL_ERROR'
    );
  }
}

